<template>
  <div v-for="film in filmsList" :key="film.id" @click="Output(film.id)">
    <img :src="film.src" :alt="film.imgAlt" />
    <p>{{ film.title }}</p>
  </div>
</template>

<script>
export default {
  data () {
    return {
      filmsList: [{ id: 0, title: 'Text' }]
    }
  },
  methods: {
    Output (id) {
      console.log(id)
      this.$router.push(`Films/${id}`)
    }
  }
}
</script>

